.search {
  width: 100%;
  height: 60px;
  position: relative; }
  .search .search-input {
    height: 60px;
    width: 100%;
    min-width: 100%;
    padding: 0;
    border-radius: 0;
    line-height: 70px;
    font-family: Roboto Slab, sans-serif;
    background-color: transparent;
    color: #333;
    font-size: 30px;
    border: none;
    outline: none;
    border-bottom: 3px solid #eee; }
  .search input:focus + .input-highlight {
    border-top: 3px solid #fbc91b; }
  .search .input-highlight {
    font-size: 30px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    line-height: 70px;
    border-top: 3px solid #333;
    position: absolute;
    font-family: Roboto Slab, sans-serif;
    left: 0;
    bottom: -3px;
    max-width: 100%;
    height: 0;
    color: transparent;
    overflow: hidden; }



